.message {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  background-color: $color-yellow;
  width: 100%;
  height: 82px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.message__text-wrapper {
  align-items: center;
  display: flex;
  margin-right: 7%;
  flex-shrink: 0;
}

.message__inner {
  display: flex;
  align-items: center;
  width: 45%;
  margin-left: 40%;
  min-width: 435px;
}

.message__text {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: $color-white;
  margin-left: 8px;

  @media (max-width: $mobiles-max) {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 21px;
  }
}

.message__wrapper {
  display: flex;
}