.banners {
  background-image: url("../img/main-background.png");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 661px;

  @media (max-width: $tablets-max) {
    min-height: 500px;
  }

  @media (max-width: $mobiles-max) {
    min-height: 405px;
  }
}

.banners__offers {
  li {
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;
    color: $color-white;
    margin-bottom: 20px;
    display: flex;
    align-items: center;

    &:last-of-type {
      margin-bottom: 0;
    }

    span {
      margin-left: 10px;
    }

    @media (max-width: $mobiles-max) {
      margin-bottom: 25px;
      padding-left: 10px;
    }
  }
}

.banners__inner {
  display: flex;

}

.banners__texts {
  width: 50%;
  margin-right: 2%;
  transition: $transition-settings;

  @media (max-width: $tablets-max) {

    margin-right: 2%;
    width: 370px;
    flex-shrink: 0;
  }

  @media (max-width: $mobiles-max) {
    width: 330px;
  }
}

.banners__picture {
  width: 45%;
  margin-top: 15px;
  transition: $transition-settings;

  @media (max-width: $tablets-max) {
    min-width: 300px;
    width: 50%;
  }

  @media (max-width: $mobiles-max) {
    min-width: 200px;
  }

  @media (max-width: 550px) {
    display: none;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}
