.modal__overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #17171785;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal__close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.modal__body {
  overflow-x: auto;
  width: calc(100% - 40px);

  background: #ffffff;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.14);
  border-radius: 12px;

  display: flex;
  flex-direction: column;
  position: relative;
  align-items: center;
  max-width: 1156px;
  max-height: fit-content;

  max-height: calc(100vh - 100px);
}
