.questions {
  background-image: url("../img/questions-background.png");
  background-repeat: no-repeat;
  min-height: 330px;
  background-size: cover;
}

.questions__inner {
  padding: 70px 0;
  h3 {
    margin: 0;
    color: #fefefe;

    @media (max-width: $mobiles-max) {
      text-align: left;

      font-size: 22px;
      font-style: normal;
      font-weight: 700;
      line-height: 27px;
      letter-spacing: 0em;
      text-align: left;
    }
  }

  @media (max-width: $mobiles-max) {
    padding: 30px 0 20px;
  }
}

.questions__subtitle {
  font-size: 22px;
  font-style: normal;
  font-weight: 500;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  color: $color-default-white;

  margin: 15px 0 65px;

  @media (max-width: $mobiles-max) {
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;

    margin: 15px 0 30px;
  }
}

.questions__list {
  display: flex;

  @media (max-width: $mobiles-max) {
    flex-direction: column;
    align-items: center;
  }
}

.questions__item {
  width: 23.5%;
  background: #fdfdfd;
  border-radius: 4px;
  margin-right: 14.75%;

  background: #fdfdfd;
  border-radius: 4px;

  @media (max-width: $mobiles-max) {
    width: 100%;
    max-width: 500px;
    margin-bottom: 22px;
    margin-right: 0;
  }

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
  }

  p {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0em;
    text-align: left;
    margin: 0 0 0 15px;
  }

  &:last-of-type {
    margin-right: 0;
  }
}
