/* stylelint-disable */
html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none !important;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 150%;
  width: 100%;
  margin: auto;
  margin: 0 auto;
  height: 100%;
  color: $font-base-color;
  background-color: $background-base-color;
}

img {
  max-width: 100%;
  height: auto;
}

a,
button {
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

  &:focus {
    outline: none !important;
  }
}

h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 34px;
  font-style: normal;
  font-weight: 700;
  line-height: 58px;
  letter-spacing: 0em;
  text-align: left;
  color: $color-white;

  text-transform: uppercase;

  margin: 85px 0 14px;

  @media (max-width: $tablets-max) {
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px;
    letter-spacing: 0em;
    text-align: left;
  }

  @media (max-width: $mobiles-max) {
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 31px;
    letter-spacing: 0em;
    text-align: left;

    margin: 30px 0 16px;
  }

  span {
    transform: rotate(-5.79deg) translate(10px, -5px);
    display: inline-block;
    position: relative;

    &:before {
      position: absolute;
      content: "";
      background-image: url("../img/frame-title.svg");
      top: -14px;
      left: -14px;
      transform: rotate(5.79deg);
      width: 230px;
      height: 80px;
      z-index: -1;
      background-repeat: no-repeat;
    }

    @media (max-width: $tablets-max) {
      &:before {
        background-image: url("../img/frame-mob.svg");

        top: -5px;
        left: -12px;
        width: 130px;
        height: 50px;
      }
    }
  }

  p {
    display: inline-block;
  }

  b {
    font-weight: bold;
    font-size: 24px;
    line-height: 36px;

    text-transform: uppercase;
    display: block;
    margin-top: 6px;

    @media (max-width: $tablets-max) {
      font-size: 18px;
      font-style: normal;
      font-weight: 700;
      line-height: 31px;
      letter-spacing: 0em;
      text-align: left;
    }

    @media (max-width: $mobiles-max) {
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: 24px;
      letter-spacing: 0em;
      text-align: left;

      margin-top: 18px;
      display: block;
    }

    p {
      background-color: $color-yellow;
    }
  }
}

h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
  letter-spacing: 0em;
  text-align: left;

  @media (max-width: $mobiles-max) {
    font-size: 22px;
    line-height: 120%;
  }
}

h3 {
  font-family: "Montserrat", sans-serif;
  color: #101a4e;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 39px;
  letter-spacing: 0em;
  text-align: left;

  span {
    background-color: $color-yellow;
  }

  @media (max-width: $mobiles-max) {
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 38px;
    letter-spacing: 0em;
    text-align: center;
    margin: 0 auto;
  }
}

h4 {
  font-family: "Montserrat", sans-serif;

  font-size: 19px;
  font-style: normal;
  font-weight: 700;
  line-height: 23px;
  letter-spacing: 0em;
  text-align: left;
  margin: 0;

  @media (max-width: $mobiles-max) {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;
  }
}

h5 {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 17px;
  line-height: 140%;
  margin: 0 0 20px 0;
}

h6 {
  font-family: "Montserrat", sans-serif;
  font-size: 17px;
  line-height: 110%;
  letter-spacing: 0.05em;
  margin: 0 0 20px 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0 0 15px 0;
}

.wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  // @media (max-width: $mobiles-max) {
  overflow: hidden;
  // }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.hidden {
  display: none i !important;
}

.show {
  display: block i !important;
}

[placeholder] {
  color: $color-placeholder;
  opacity: 1;
  transition: opacity 0.3s ease;
}

:-webkit-input-placeholder {
  color: $color-placeholder;
  opacity: 1;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:-moz-placeholder {
  color: $color-placeholder;
  opacity: 1;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

::-moz-placeholder {
  color: $color-placeholder;
  opacity: 1;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:-ms-input-placeholder {
  color: $color-placeholder;
  opacity: 1;
  -ms-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus[placeholder] {
  opacity: 0;
  outline: none;
  transition: opacity 0.3s ease;
}

:focus::-webkit-input-placeholder {
  opacity: 0;
  outline: none;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus::-moz-placeholder {
  opacity: 0;
  outline: none;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus:-moz-placeholder {
  opacity: 0;
  outline: none;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus:-ms-input-placeholder {
  opacity: 0;
  outline: none;
  -ms-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

a {
  color: $color-black;
  text-decoration: none;
  transition: all 0.3s;
}

input,
textarea {
  color: $color-black !important;
  opacity: 1 !important;
}

table {
  width: 100%;
  border-collapse: collapse;
}

.container {
  width: 100%;
  max-width: $container;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;

  @media (max-width: $tablets-max) {
    padding-right: 40px;
    padding-left: 40px;
  }

  @media (max-width: $mobiles-max) {
    padding-right: 20px;
    padding-left: 20px;
  }

  &--wide {
    max-width: 100%;
    padding-right: 0;
    padding-left: 0;
  }

  &--narrow {
    max-width: 754px;
    @media (max-width: $mobiles-max) {
      padding-right: 0;
      padding-left: 0;
    }
  }

  &--modal {
    max-width: 1130px;
    margin: auto;
  }
}

.v-select,
.vs__dropdown-toggle,
.v-select input {
  cursor: pointer !important;
}

#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.error {
  border: 2px solid $color-red !important;
}

.grecaptcha-badge {
  display: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.scroll-off {
  overflow: hidden;
}



::-webkit-scrollbar-button {
  background-image: none;
  background-repeat: no-repeat;
  width: 5px;
  height: 0px
}

::-webkit-scrollbar-track {
  background-color: #E0E0E0;
}

::-webkit-scrollbar-thumb {
  -webkit-border-radius: 0px;
  border-radius: 0px;
  background-color: $color-blue;
  position: relative;
  z-index: 1;
}

::-webkit-scrollbar-thumb:hover {
  background-color: $color-blue;
}

::-webkit-resizer {
  background-image: none;
  background-repeat: no-repeat;
  width: 4px;
  height: 0px
}

::-webkit-scrollbar {
  width: 6px;
}