.services {
  margin-top: 42px;
  margin-bottom: 50px;

  @media (max-width: $mobiles-max) {
    margin-top: 22px;

    margin-bottom: 85px;

    .swiper-slide {
      width: 90%;
    }

    .swiper-container {
      overflow: visible;
    }

    .swiper-pagination {
      position: absolute;
      bottom: -30px !important;
      left: 50% !important;
      width: 100px !important;
      transform: translate(-50%, -50%);
      display: flex;
      align-items: center;
      width: 50px;
      justify-content: space-between;
    }
    .swiper-pagination-bullet {
      width: 8px;
      height: 8px;
      border: 1.5px solid $color-blue;
      background-color: #fff;
      opacity: 1;
      border-radius: 0;
    }
    .swiper-pagination-bullet-active {
      width: 12px;
      height: 12px;
      background: $color-blue;
      border-radius: 0;
    }

    .button-prev,
    .button-next {
      &:after {
        display: none;
      }
      top: auto;
      bottom: 0;
      width: 32px;
      height: 32px;
      background-color: transparent;
      border: 1px solid $color-blue;

      position: absolute;
      top: 50%;
      z-index: 10;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .button-prev {
      bottom: -35px !important;
      left: 18.5% !important;
      svg {
        transform: rotate(180deg);
      }
    }

    .button-next {
      top: auto;
      bottom: -35px !important;
      right: 18.5% !important;
      left: auto;
    }

    .button-prev {
      top: auto;
      bottom: 10px;
      right: auto;
      left: 43.5%;
    }

    .button-prev.swiper-button-disabled,
    .button-next.swiper-button-disabled {
      opacity: 0.35;
      cursor: auto;
      pointer-events: none;
    }
  }
}

.services__inner {
  h3 {
    text-align: center;
  }
}

.services__list {
  display: flex;
  flex-wrap: wrap;

  margin-top: 45px;

  @media (max-width: $mobiles-max) {
  }
}

.services__item {
  width: 32%;
  margin-right: 2%;
  display: flex;
  align-items: center;

  padding: 30px 10px 30px 30px;

  min-height: 150px;
  margin-bottom: 20px;

  background: $color-default-white;
  box-shadow: 0px 16px 48px 10px rgba(72, 154, 227, 0.1);
  border-radius: 17px;
  display: flex;
  flex-direction: row;

  @media (min-width: $desktops) {
    &:nth-child(3n) {
      margin-right: 0;
    }
  }

  @media (max-width: $tablets-max) {
    width: 48%;
    margin-right: 4%;

    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }

  @media (max-width: $mobiles-max) {
    padding: 40px 10px 30px 25px;
    min-height: auto;
    margin-right: 20px;
    min-height: 135px;
  }

  p {
    font-size: 19px;
    font-style: normal;
    font-weight: 700;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: left;

    margin: 0 0 0 20px;

    @media (max-width: $mobiles-max) {
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: 22px;
      letter-spacing: 0em;
      text-align: left;
    }
  }
}

.services__icon {
  width: 60px;
  flex-shrink: 0;
  height: 60px;

  @media (max-width: $mobiles-max) {
    width: 49px;
    height: 49px;
  }

  img {
    object-fit: contain;
    height: 100%;
    width: 100%;
  }
}

.services__swiper {
  @media (max-width: $mobiles-max) {
    margin-top: 25px;
  }
}
