.footer {
  background-color: #f6f6f6;
  min-height: 60px;
}

.footer__menu {
  display: flex;
  padding: 18px 0;
  justify-content: space-between;

  @media (max-width: $mobiles-max) {
    flex-direction: column;
    align-items: center;
  }
}

.footer__menu-item {
  a {
    color: #5f5f5f;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;

    @media (max-width: $mobiles-max) {
      font-size: 16px;
      font-style: normal;
      font-weight: 500;
      line-height: 20px;
      letter-spacing: 0em;
      text-align: left;
    }
  }

  @media (max-width: $mobiles-max) {
    margin-bottom: 8px;
    &:first-of-type {
      order: 3;
    }
    &:nth-of-type(2) {
      order: 2;
    }
    &:nth-of-type(3) {
      order: 1;
    }
  }
}
