.form {
  background-image: url("../img/footer.png");
  min-height: 587px;

  padding: 48px 0;
  background-size: cover;
  background-repeat: no-repeat;

  @media (max-width: $mobiles-max) {
    padding: 30px 0;
    min-height: 459px;
  }
}

.form__image {
  width: 49%;
  margin-right: 2%;

  height: 456px;
  position: relative;

  &:after {
    position: absolute;
    content: "";
    background-image: url("../img/arrow-3.svg");
    width: 300px;
    height: 180px;
    bottom: -35px;
    right: 22px;
    background-repeat: no-repeat;
    z-index: 0;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
    z-index: 1;
    position: relative;
  }

  @media (max-width: $mobiles-max) {
    display: none;
  }
}

.form__title {
  font-size: 26px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 0em;
  color: #fefefe;
  text-align: center;

  margin: 0;

  @media (max-width: $mobiles-max) {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: center;
  }
}

.form__subtitle {
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: center;
  color: #ededed;

  margin: 15px 0 30px;

  @media (max-width: $mobiles-max) {
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 17px;
    letter-spacing: 0em;
    text-align: center;

    padding: 0 14px;
    margin: 10px 0 8px;
  }
}

.form__fields {
  width: 49%;
  flex-grow: 1;
  flex-shrink: 0;

  padding: 40px 40px 64px 40px;

  background: #1e87f2;
  border: 3px solid rgba(255, 255, 255, 0.1);
  box-sizing: border-box;
  border-radius: 12px;

  min-width: 385px;

  @media (max-width: $tablets-max) {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
    min-width: auto;

    padding: 28px 12px 25px 12px;
  }
}

.form__body {
  display: flex;
  position: relative;

  &:after {
    position: absolute;
    content: "";
    background-image: url(../img/arrow-2.svg);
    right: -260px;
    bottom: -65px;
    width: 260px;
    height: 115px;
    background-repeat: no-repeat;
  }
}

.form__fieldset {
  border: none;
  padding: 0;
}

.form__field {
  margin-bottom: 20px;
  @media (max-width: $mobiles-max) {
    padding-left: 0;
    width: 100%;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.form__field-wrapper {
  display: flex;
  flex-direction: column;
}

.form__error {
  color: $color-red;
  display: block;
  text-align: right;
  @media (max-width: $mobiles-max) {
    text-align: left;
  }
}

.form__label {
  color: #fefefe;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0px;
  text-align: left;

  margin-bottom: 6px;
}

.form__input {
  width: 100%;
  height: 50px;
  padding: 0 20px;
  border: none;
  transition: $transition-settings;

  background: #fdfdfd;
  border-radius: 4px;

  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0em;
  text-align: left;
  color: #101a4e;

  @media (max-width: $mobiles-max) {
    max-width: 100%;
  }
  &--filled {
    border: 1px solid $color-black;
  }
  &--error {
    border: 1px solid $color-red;
  }
}

.form__radio {
  display: none;
}

.form__radio + label {
  position: relative;
  padding: 0 0 15px 20px;
  cursor: pointer;
}

.form__radio + label:before {
  content: "";
  position: absolute;
  top: 5px;
  left: 0;
  width: 10px;
  height: 10px;
  border: 1px solid $color-gray;
  border-radius: 50%;
  background: transparent;
}

.form__radio + label::after {
  content: "";
  position: absolute;
  top: 7px;
  left: 2px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: $color-gold;
  opacity: 0;
  transition: $transition-settings;
}

.form__radio:checked + label:before {
  border: 1px solid $color-gold;
}
.form__radio:checked + label:after {
  opacity: 1;
}

.form__textarea {
  resize: none;
  width: 100%;
  max-width: 311px;
  max-height: 100px;
  padding: 20px;
  border: 1px solid $color-gray;
  transition: $transition-settings;
  @media (max-width: $mobiles-max) {
    max-width: 100%;
  }
  &--filled {
    border: 1px solid $color-black;
  }
  &--error {
    border: 1px solid $color-red;
  }
}
