.case {
  padding: 55px 0 0;
  margin-bottom: 60px;

  @media (max-width: $mobiles-max) {
    padding: 50px 0 0;
    margin-bottom: 57px;
  }
}

.case__slide {
  display: flex;
  height: auto !important;
}

.case__inner {
  margin-top: 60px;

  @media (max-width: $mobiles-max) {
    margin-top: 50px;
  }
}

.case__title {
  color: #101a4e;
  text-align: center;
}

.case__details {
  display: flex;
  margin-top: 10px;
  flex-wrap: wrap;

  .case__detail:last-of-type {
    flex-grow: 1;
    width: 100%;
  }

  @media (max-width: $mobiles-max) {
    margin-top: 16px;

    .case__detail {
      width: 65%;
      &:nth-of-type(2n) {
        width: 35%;
      }
    }

    &:last-of-type {
      flex-grow: 1;
      width: 100%;
    }
  }
}

.case__detail {
  width: 50%;
  margin-bottom: 15px;
  p {
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;
    margin: 0 0 10px 0;

    @media (max-width: $mobiles-max) {
      font-size: 18px;
      font-style: normal;
      font-weight: 600;
      line-height: 22px;
      letter-spacing: 0em;
      text-align: left;
    }
  }

  span {
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;

    @media (max-width: $mobiles-max) {
      font-size: 16px;
      font-style: normal;
      font-weight: 500;
      line-height: 20px;
      letter-spacing: 0em;
      text-align: left;
    }
  }
}

.case__data-block {
  p {
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: center;
  }
}

.case__data-numbers {
  display: flex;
  width: 100%;
}

.case__data {
  @media (max-width: $mobiles-max) {
    margin-top: 20px;
    margin-bottom: 0px;
  }
}

.case__data-number {
  display: flex;
  flex-direction: column;
  width: 46%;

  @media (max-width: $mobiles-max) {
    width: 50%;
  }

  b {
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: center;
  }

  span {
    font-style: normal;
    font-weight: 200;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    margin-top: 5px;

    color: #101a4e;
  }
}

.case__now {
  color: #fefefe;
  background: $color-blue;
  padding: 20px;
  position: relative;
  padding: 7px 0;

  &:after {
    content: "";
    position: absolute;

    border: 17px solid transparent;
    right: -36px;
    top: 0px;

    border-left: 20px solid $color-blue;
  }
}

.case__then {
  color: #444b54;

  background: #f6f6f6;
  padding: 20px;
  position: relative;
  padding: 7px 0;

  &:after {
    content: "";
    position: absolute;
    border: 16px solid transparent;
    border-left: 20px solid #0c7aeb;
    right: -35px;
    top: 1px;
    border-left: 20px solid #f6f6f6;
    z-index: 2;
  }

  &:before {
    content: "";
    position: absolute;
    left: 0px;
    top: 0px;
    border: 18px solid transparent;
    border-left: 20px solid #fff;

    z-index: 2;
  }
}

.case__body {
  padding: 13px;

  @media (min-width: $tablets) {
    h4 {
      min-height: 67px;
    }
  }

  @media (max-width: $mobiles-max) {
    padding: 18px 22px 0;
  }
}

.case__data-item {
  margin-bottom: 6px;
}

.case__swiper-wrapper {
  position: relative;

  .swiper-container {
    padding: 0px 40px 60px;
    margin-left: -40px;
    margin-right: -40px;
  }

  .swiper-pagination {
    position: absolute;
    bottom: 15px;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    width: 50px;
    justify-content: space-between;

    @media (max-width: 1199px) {
      width: 70px;
    }

    @media (max-width: 999px) {
      width: 80px;
    }
    @media (max-width: $mobiles-max) {
      width: 80px;
    }
  }
  .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
    border: 1.5px solid $color-blue;
    background-color: #fff;
    opacity: 1;
    border-radius: 0;
  }
  .swiper-pagination-bullet-active {
    width: 12px;
    height: 12px;
    background: $color-blue;
    border-radius: 0;
  }

  .swiper-button-prev,
  .swiper-button-next {
    &:after {
      display: none;
    }
    top: auto;
    bottom: 0;
    width: 32px;
    height: 32px;
    background-color: transparent;
    border: 1px solid $color-blue;
  }

  .swiper-button-prev {
    svg {
      transform: rotate(180deg);
    }
  }

  .swiper-button-next {
    top: auto;
    bottom: 10px;
    right: 43.5%;
    left: auto;

    @media (max-width: 1199px) {
      right: 40.5%;
    }

    @media (max-width: 999px) {
      right: 37.5%;
    }

    @media (max-width: $mobiles-max) {
      right: 26.5%;
    }
  }

  .swiper-button-prev {
    top: auto;
    bottom: 10px;
    right: auto;
    left: 43.5%;

    @media (max-width: 1199px) {
      left: 40.5%;
    }

    @media (max-width: 999px) {
      left: 37.5%;
    }

    @media (max-width: $mobiles-max) {
      left: 26.5%;
    }
  }
}

.case__image-swiper {
  filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.1));
  border-radius: 17px;
  .swiper-picture-pagination {
    position: absolute;
    bottom: 15px;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    width: 50px;
    justify-content: space-between;

    @media (max-width: 1199px) {
      width: 70px;
    }

    @media (max-width: 999px) {
      width: 80px;
    }

    .swiper-pagination-bullet {
      width: 8px;
      height: 8px;
      border: 1.5px solid $color-blue;
      background-color: #fff;
      opacity: 1;
      border-radius: 0;
    }
    .swiper-pagination-bullet-active {
      width: 12px;
      height: 12px;
      background: $color-blue;
      border-radius: 0;
    }
  }

  .swiper-pagination-bullets {
    left: 50% !important;
    width: auto !important;
    z-index: 2 !important;
  }

  .swiper-picture-prev,
  .swiper-picture-next {
    &:after {
      display: none;
    }
    top: auto;
    bottom: 0;
    width: 32px;
    height: 32px;
    background-color: transparent;

    position: absolute;
    top: 50%;
    z-index: 10;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;

    top: 50%;
    transform: translate(0, -50%);
  }

  .swiper-picture-prev {
    left: 0 !important;
    svg {
      transform: rotate(180deg);
    }
  }

  .swiper-picture-next {
    right: 0 !important;
  }

  .swiper-picture-prev.swiper-button-disabled,
  .swiper-picture-next.swiper-button-disabled {
    opacity: 0.35;
    cursor: auto;
    pointer-events: none;
  }
}

.case__slide {
  width: 32% !important;
  margin-right: 2%;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  box-shadow: 0px 16px 48px 10px rgba(72, 154, 227, 0.1);

  @media (min-width: $desktops) {
    &:last-of-type {
      margin-right: 0;
    }
  }

  @media (max-width: $tablets-max) {
    width: 48% !important;
    margin-right: 4%;
  }

  @media (min-width: $tablets) {
    &:last-of-type {
      margin-right: 0;
    }
  }

  @media (max-width: $mobiles-max) {
    width: 100% !important;
  }

  .swiper-container {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    overflow: hidden;
    list-style: none;
    padding: 0;
    z-index: 1;
  }
}

.case__additional-item {
  @media (max-width: $mobiles-max) {
    margin-bottom: 0;
    padding-bottom: 18px;
  }
}

.case__additional-texts {
  p {
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    margin: 0;
  }

  @media (max-width: $mobiles-max) {
    padding: 16px 0 18px;

    p {
      font-size: 20px;
      font-style: normal;
      font-weight: 700;
      line-height: 24px;
      letter-spacing: 0em;
      text-align: left;
      margin: 0;
    }

    span {
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 17px;
      letter-spacing: 0em;
      text-align: left;
    }
  }
}

.case__picture {
  width: 100%;
  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.case__additional-text {
  span {
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 17px;
    letter-spacing: 0em;
    text-align: left;
  }
  @media (max-width: $mobiles-max) {
    padding-top: 18px;

    li {
      display: flex;
      margin-bottom: 20px;

      svg {
        flex-shrink: 0;
        transform: translate(0, 10px);
      }
      span {
        margin-left: 18px;
      }
    }
  }
}

.case--fastview {
  width: 100%;
  padding: 25px 12px;
  margin-bottom: 0;
  overflow-x: auto;

  .case__inner {
    margin-top: 0;
  }

  .case__details {
    margin-top: 22px;
    display: flex;
    flex-direction: column;
  }

  .case__detail {
    margin-bottom: 20px;
  }

  .case__fastview-wrapper {
    display: flex;
  }

  .case__additional-wrapper {
    width: 40%;
    margin-right: 11%;

    &:last-of-type {
      margin-right: 0;
    }
    p {
      margin-bottom: 10px;
    }
  }

  .case__additional-texts {
    display: flex;
    padding: 0 13px;
    margin-top: 13px;
  }

  .case__additional-text {
    span {
      margin-left: 18px;
    }

    svg {
      flex-shrink: 0;
    }

    li {
      display: flex;
      align-items: center;
      margin-bottom: 22px;

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }

  .case__picture {
    width: 49%;
    margin-right: 2%;
    height: 355px;
  }

  .case__titles {
    width: 49%;
  }

  .case__body {
    margin-top: 10px;
  }

  .case__data {
    display: flex;

    .case__data-block {
      width: 32%;
      margin-right: 2%;

      &:last-of-type {
        margin-right: 0;
      }
    }
  }
}
