.header {
  transition: $transition-settings;
  width: 100%;
  z-index: 10;
  background-color: $color-default-white;

  min-height: 80px;
  padding: 30px 0 20px;
}

.header__top {
  display: flex;
  justify-content: space-between;
}

.header__menu-item {
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: right;

  @media (max-width: $mobiles-max) {
    margin-bottom: 10px;
  }
}

.header__menu {
  display: flex;
  width: 35%;
  align-items: center;
  justify-content: space-between;
  transition: $transition-settings;

  @media (max-width: $tablets-max) {
    width: 45%;
  }

  @media (max-width: $mobiles-max) {
    margin-top: 10px;
    align-items: baseline;
    background: $color-default-white;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}
