/* stylelint-disable */
html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none !important;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 150%;
  width: 100%;
  margin: auto;
  margin: 0 auto;
  height: 100%;
  color: #111111;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
}

a,
button {
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
a:focus,
button:focus {
  outline: none !important;
}

h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 34px;
  font-style: normal;
  font-weight: 700;
  line-height: 58px;
  letter-spacing: 0em;
  text-align: left;
  color: #fefefe;
  text-transform: uppercase;
  margin: 85px 0 14px;
}
@media (max-width: 1023px) {
  h1 {
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px;
    letter-spacing: 0em;
    text-align: left;
  }
}
@media (max-width: 767px) {
  h1 {
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 31px;
    letter-spacing: 0em;
    text-align: left;
    margin: 30px 0 16px;
  }
}
h1 span {
  transform: rotate(-5.79deg) translate(10px, -5px);
  display: inline-block;
  position: relative;
}
h1 span:before {
  position: absolute;
  content: "";
  background-image: url("../img/frame-title.svg");
  top: -14px;
  left: -14px;
  transform: rotate(5.79deg);
  width: 230px;
  height: 80px;
  z-index: -1;
  background-repeat: no-repeat;
}
@media (max-width: 1023px) {
  h1 span:before {
    background-image: url("../img/frame-mob.svg");
    top: -5px;
    left: -12px;
    width: 130px;
    height: 50px;
  }
}
h1 p {
  display: inline-block;
}
h1 b {
  font-weight: bold;
  font-size: 24px;
  line-height: 36px;
  text-transform: uppercase;
  display: block;
  margin-top: 6px;
}
@media (max-width: 1023px) {
  h1 b {
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 31px;
    letter-spacing: 0em;
    text-align: left;
  }
}
@media (max-width: 767px) {
  h1 b {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    margin-top: 18px;
    display: block;
  }
}
h1 b p {
  background-color: #FFA830;
}

h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
  letter-spacing: 0em;
  text-align: left;
}
@media (max-width: 767px) {
  h2 {
    font-size: 22px;
    line-height: 120%;
  }
}

h3 {
  font-family: "Montserrat", sans-serif;
  color: #101a4e;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 39px;
  letter-spacing: 0em;
  text-align: left;
}
h3 span {
  background-color: #FFA830;
}
@media (max-width: 767px) {
  h3 {
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 38px;
    letter-spacing: 0em;
    text-align: center;
    margin: 0 auto;
  }
}

h4 {
  font-family: "Montserrat", sans-serif;
  font-size: 19px;
  font-style: normal;
  font-weight: 700;
  line-height: 23px;
  letter-spacing: 0em;
  text-align: left;
  margin: 0;
}
@media (max-width: 767px) {
  h4 {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;
  }
}

h5 {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 17px;
  line-height: 140%;
  margin: 0 0 20px 0;
}

h6 {
  font-family: "Montserrat", sans-serif;
  font-size: 17px;
  line-height: 110%;
  letter-spacing: 0.05em;
  margin: 0 0 20px 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0 0 15px 0;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.hidden {
  display: none i !important;
}

.show {
  display: block i !important;
}

[placeholder] {
  color: #D8D8D8;
  opacity: 1;
  transition: opacity 0.3s ease;
}

:-webkit-input-placeholder {
  color: #D8D8D8;
  opacity: 1;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:-moz-placeholder {
  color: #D8D8D8;
  opacity: 1;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

::-moz-placeholder {
  color: #D8D8D8;
  opacity: 1;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:-ms-input-placeholder {
  color: #D8D8D8;
  opacity: 1;
  -ms-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus[placeholder] {
  opacity: 0;
  outline: none;
  transition: opacity 0.3s ease;
}

:focus::-webkit-input-placeholder {
  opacity: 0;
  outline: none;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus::-moz-placeholder {
  opacity: 0;
  outline: none;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus:-moz-placeholder {
  opacity: 0;
  outline: none;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus:-ms-input-placeholder {
  opacity: 0;
  outline: none;
  -ms-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

a {
  color: #111111;
  text-decoration: none;
  transition: all 0.3s;
}

input,
textarea {
  color: #111111 !important;
  opacity: 1 !important;
}

table {
  width: 100%;
  border-collapse: collapse;
}

.container {
  width: 100%;
  max-width: 1190px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
}
@media (max-width: 1023px) {
  .container {
    padding-right: 40px;
    padding-left: 40px;
  }
}
@media (max-width: 767px) {
  .container {
    padding-right: 20px;
    padding-left: 20px;
  }
}
.container--wide {
  max-width: 100%;
  padding-right: 0;
  padding-left: 0;
}
.container--narrow {
  max-width: 754px;
}
@media (max-width: 767px) {
  .container--narrow {
    padding-right: 0;
    padding-left: 0;
  }
}
.container--modal {
  max-width: 1130px;
  margin: auto;
}

.v-select,
.vs__dropdown-toggle,
.v-select input {
  cursor: pointer !important;
}

#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.error {
  border: 2px solid #EE7D7D !important;
}

.grecaptcha-badge {
  display: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.scroll-off {
  overflow: hidden;
}

::-webkit-scrollbar-button {
  background-image: none;
  background-repeat: no-repeat;
  width: 5px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background-color: #E0E0E0;
}

::-webkit-scrollbar-thumb {
  -webkit-border-radius: 0px;
  border-radius: 0px;
  background-color: #0C7AEB;
  position: relative;
  z-index: 1;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #0C7AEB;
}

::-webkit-resizer {
  background-image: none;
  background-repeat: no-repeat;
  width: 4px;
  height: 0px;
}

::-webkit-scrollbar {
  width: 6px;
}

@keyframes move-gradient {
  from {
    background-position-x: left;
  }
  to {
    background-position-x: right;
  }
}
.slide-left-enter-active {
  transition: all 0.6s;
}

.slide-left-leave-active {
  transition: all 0.6s;
}

.slide-left-enter,
.slide-left-leave-to {
  transform: translateX(100%);
}

.fade-enter-active,
.fade-leave-active {
  transition-property: opacity;
  transition-duration: 0.3s;
}

.fade-enter-active {
  transition-delay: 0.3s;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}

.header {
  transition: 0.3s all;
  width: 100%;
  z-index: 10;
  background-color: #ffffff;
  min-height: 80px;
  padding: 30px 0 20px;
}

.header__top {
  display: flex;
  justify-content: space-between;
}

.header__menu-item {
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: right;
}
@media (max-width: 767px) {
  .header__menu-item {
    margin-bottom: 10px;
  }
}

.header__menu {
  display: flex;
  width: 35%;
  align-items: center;
  justify-content: space-between;
  transition: 0.3s all;
}
@media (max-width: 1023px) {
  .header__menu {
    width: 45%;
  }
}
@media (max-width: 767px) {
  .header__menu {
    margin-top: 10px;
    align-items: baseline;
    background: #ffffff;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}

.footer {
  background-color: #f6f6f6;
  min-height: 60px;
}

.footer__menu {
  display: flex;
  padding: 18px 0;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .footer__menu {
    flex-direction: column;
    align-items: center;
  }
}

.footer__menu-item a {
  color: #5f5f5f;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: left;
}
@media (max-width: 767px) {
  .footer__menu-item a {
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;
  }
}
@media (max-width: 767px) {
  .footer__menu-item {
    margin-bottom: 8px;
  }
  .footer__menu-item:first-of-type {
    order: 3;
  }
  .footer__menu-item:nth-of-type(2) {
    order: 2;
  }
  .footer__menu-item:nth-of-type(3) {
    order: 1;
  }
}

.form {
  background-image: url("../img/footer.png");
  min-height: 587px;
  padding: 48px 0;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .form {
    padding: 30px 0;
    min-height: 459px;
  }
}

.form__image {
  width: 49%;
  margin-right: 2%;
  height: 456px;
  position: relative;
}
.form__image:after {
  position: absolute;
  content: "";
  background-image: url("../img/arrow-3.svg");
  width: 300px;
  height: 180px;
  bottom: -35px;
  right: 22px;
  background-repeat: no-repeat;
  z-index: 0;
}
.form__image img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  z-index: 1;
  position: relative;
}
@media (max-width: 767px) {
  .form__image {
    display: none;
  }
}

.form__title {
  font-size: 26px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 0em;
  color: #fefefe;
  text-align: center;
  margin: 0;
}
@media (max-width: 767px) {
  .form__title {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: center;
  }
}

.form__subtitle {
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: center;
  color: #ededed;
  margin: 15px 0 30px;
}
@media (max-width: 767px) {
  .form__subtitle {
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 17px;
    letter-spacing: 0em;
    text-align: center;
    padding: 0 14px;
    margin: 10px 0 8px;
