.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  outline: none !important;
  border: none;
  transition: 0.3s all;
  cursor: pointer;
  flex-shrink: 0;
}
.button--toggle {
  width: 20px;
  position: absolute;
  height: 10px;
  display: flex;
  background-color: #ffffff;
  align-items: center;
  justify-content: center;
  transition: 0.3s all;
  right: 20px;
  top: 35px;
  padding: 0;
  z-index: 3;
}
.button--toggle:before {
  content: "";
  position: absolute;
  width: 2px;
  height: 20px;
  background-color: #111111;
  transform: rotate(45deg);
  transition: 0.3s all;
}
.button--toggle:after {
  content: "";
  position: absolute;
  width: 2px;
  height: 20px;
  background-color: #111111;
  transform: rotate(-45deg);
  transition: 0.3s all;
}
.button--toggle.close span {
  background-color: #111111;
  height: 2px;
  width: 20px;
  margin-top: 6px;
}
.button--toggle.close:before {
  height: 20px;
  top: -6px;
  background-color: #111111;
  transform: rotate(90deg);
}
.button--toggle.close:after {
  height: 20px;
  bottom: -12px;
  background-color: #111111;
  transform: rotate(90deg);
}
@media (min-width: 1023px) {
  .button--toggle {
    display: none;
  }
}
@media (max-width: 1023px) {
  .button--toggle {
    display: flex;
  }
