.advantages {
  margin-top: -90px;
  transition: $transition-settings;

  @media (max-width: $tablets-max) {
    margin-top: 0;
    padding-top: 50px;
  }
}

.advantages__list {
  display: flex;

  @media (max-width: $mobiles-max) {
    flex-wrap: wrap;
  }
}

.advantages__item {
  background: $color-default-white;
  box-shadow: 0px 16px 48px 10px rgba(72, 154, 227, 0.1);
  border-radius: 17px;
  display: flex;
  flex-direction: column;

  padding: 38px 0 25px;
  align-items: center;

  min-height: 230px;
  width: 23.5%;
  margin-right: 2%;

  &:last-of-type {
    margin-right: 0;
  }

  p {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: center;

    margin: 45px 0 20px;
    max-width: 155px;

    @media (max-width: $mobiles-max) {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 17px;
      letter-spacing: 0em;
      text-align: center;
      margin: 20px 0;
    }
  }

  @media (max-width: $mobiles-max) {
    width: 48%;
    margin-right: 4%;
    padding: 22px 0 5px;
    min-height: auto;
    margin-bottom: 20px;

    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }
}

.advantages__icon {
  width: 60px;
  height: 60px;

  @media (max-width: $mobiles-max) {
    width: 36px;
    height: 36px;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}
