.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  outline: none !important;
  border: none;
  transition: $transition-settings;
  cursor: pointer;
  flex-shrink: 0;

  &--toggle {
    width: 20px;
    position: absolute;
    height: 10px;
    display: flex;
    background-color: $color-default-white;
    align-items: center;
    justify-content: center;
    transition: $transition-settings;
    right: 20px;
    top: 35px;
    padding: 0;
    z-index: 3;

    &:before {
      content: "";
      position: absolute;
      width: 2px;
      height: 20px;
      background-color: $color-black;
      transform: rotate(45deg);
      transition: $transition-settings;
    }

    &:after {
      content: "";
      position: absolute;
      width: 2px;
      height: 20px;
      background-color: $color-black;
      transform: rotate(-45deg);
      transition: $transition-settings;
    }

    &.close {
      span {
        background-color: $color-black;
        height: 2px;
        width: 20px;
        margin-top: 6px;
      }

      &:before {
        height: 20px;
        top: -6px;
        background-color: $color-black;
        transform: rotate(90deg);
      }

      &:after {
        height: 20px;
        bottom: -12px;
        background-color: $color-black;
        transform: rotate(90deg);
      }
    }

    @media (min-width: $tablets-max) {
      display: none;
    }

    @media (max-width: $tablets-max) {
      display: flex;
    }
  }

  &--yellow {
    background: $color-yellow;
    color: $color-white;
    box-shadow: 0px 3px 0px #be7107;
    border-radius: 4px;

    margin-top: 40px;

    padding: 18px 0;

    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: center;

    &:hover {
      background: #be7107;
    }
  }

  &--banner {
    width: 41.5%;
    position: relative;
    min-width: 220px;

    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/arrow-right.svg");
      width: 32px;
      height: 12px;
      right: 25px;
      top: 40%;
    }

    @media (max-width: $mobiles-max) {
      min-width: auto;
      width: 71.5%;
      margin-top: 20px;
    }
  }

  &--submit {
    margin-top: 28px;
    padding: 14px 0;
    position: relative;

    svg {
      margin-left: 10px;
    }

    @media (max-width: $mobiles-max) {
      margin-top: 20px;
    }
  }

  &--more {
    background-color: $color-white;
    cursor: pointer;
    position: relative;
    transition: $transition-settings;

    border: 1px solid #0c7aeb;
    box-sizing: border-box;
    border-radius: 4px;

    height: 32px;
    width: 32px;
    margin: 24px auto 0;

    &::before {
      position: absolute;
      content: "";
      width: 18px;
      height: 1px;
      background-color: $color-blue;
      right: 6px;
      top: 15px;
      transition: $transition-settings;
    }

    &::after {
      position: absolute;
      content: "";
      width: 1px;
      height: 18px;
      background-color: $color-blue;
      right: 14px;
      top: 7px;
      transition: $transition-settings;
    }

    @media (max-width: $mobiles-max) {
      margin: 0 auto;
      padding-bottom: 18px;
    }
  }

  &--active {
    &::before {
      display: none;
    }

    &::after {
      background-color: $color-blue;
      transform: rotate(90deg);
    }
  }

  .icon {
    transition: $transition-settings;
  }

  &:hover {
    p {
      color: $color-black;
    }
  }
}
